<template>
  <div class="page has-navbar" v-nav="{title: '', showBackButton: true, onBackButtonClick: back}">
    <div class="page-content padding-top">

      <div class="item item-icon-right" @click="showAlert()">
        Alert <span class="note"></span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right" @click="showConfirm()">
        Confirm <span class="note"></span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right" @click="showAlertIOS()">
        iOS Alert <span class="note">iOS </span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right" @click="showConfirmIOS()">
        iOS Confirm <span class="note">iOS </span>
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right" @click="showCustomPopup()">
        Custom Popup
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-divider">
        Other Effects
      </div>

      <div class="item item-icon-right" @click="showAlert('scale')">
        Scale
        <i class="icon ion-ios-arrow-right"></i>
      </div>

      <div class="item item-icon-right" @click="showAlert('slide')">
        Slide
        <i class="icon ion-ios-arrow-right"></i>
      </div>

    </div>

    <popup effect='scale' title="" :buttons="customPopupButtons" v-ref:custom_popup>
      <p>
        
      </p>
    </popup>
  </div>
</template>
<style lang="scss" scoped>

  .item-divider:first-of-type {
    border-top: none;
  }

  .item-icon-right {
    .icon {
      right: 0;
      color: #ccc;
      font-size: 24px;
    }

    span.note {
      color: #aaa;
      font-size: 13px;
      float: right;
      margin-right: 15px;
    }
  }

</style>
<script>
  import {Popup} from 'vonic'

  export default{
    components: {
      Popup
    },

    data(){
      return {
        customPopupButtons: [
          {
            text: ' - 1',
            theme: 'assertive'
          },
          {
            text: ' - 2',
            theme: 'light'
          }
        ]
      }
    },

    ready() {

    },

    methods: {
      back() {
        $router.back({path: '/'})
      },

      showAlert(effect) {
        $alert.show({
          content: 'Hello!',
          okTheme: 'positive',
          effect: effect || 'default'
        }).then(() => {
          console.log('alert hide.')
        })
      },

      showConfirm() {
        $confirm.show({
          content: '""?',
          okText: ''
        }).then((res) => {
          console.log('confirm result: ', res)
        })
      },

      showAlertIOS() {
        $alert_ios.show({
          title: '',
          content: 'Safari',
          okText: ''
        }).then(() => {
          console.log('alert hide.')
        })
      },

      showConfirmIOS() {
        $confirm_ios.show({
          title: '""?',
          cancelText: '',
          okText: ''
        }).then((res) => {
          console.log('confirm result: ', res)
        })
      },

      showCustomPopup() {
        let popup = this.$refs.custom_popup
        popup.show().then((buttonIndex) => {
          console.log('buttonIndex =>', buttonIndex)
        })
      }
    }
  }
</script>



